.footer-box {
    width: 100vw;
    min-height: 300px;
    margin: 0;
    display: block;
    background: #1e1e1e;
    box-shadow: -1px -1px 4px rgb(155, 151, 151);
}

.footer-box1 {
    width: 100%;
    height: fit-content;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.footer-box2 {
    width: 100%;
    height: 30%;
    display: block;
    text-align: center;
    color: white;
}

.bx2 {
    padding: 0%;
    width: 170px;
    margin: 5px 10px;
    height: fit-content;
    color: white;
    overflow-wrap: break-word;
}

.bx2 input {
    width: 90%;
}

.bx2 h4 {
    margin-top: 10px;
}

.f {
    margin: 4px 4px;
    width: 30px;
    height: 30px;
}

.bx2 input {
    outline: none;
    border: none;
    border-radius: 5px;
    height: 25px;
    margin: 5px 0px;
}

.bx2 button {
    width: 100px;
    height: 28px;
    border-radius: 3px;
    outline: none;
    background: green;
    color: white;
}

.bx2 li {
    cursor: pointer;
}

#foot a {
    text-decoration: none;
}

#foot a:hover {
    color: orangered;
}